import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import * as moment from 'moment';
import {CareGiverProvider, CareGiver} from '../../providers/care-giver/care-giver';
import {CareType, SelectableCareTypes, SelectableCareType} from '../../providers/care-type/care-type';
import {createDefaultProvidedCare, ProvidedCare} from "../../providers/provided-care-service/provided-care-service";

@IonicPage()
@Component({
  selector: 'page-event-modal',
  templateUrl: 'event-modal.html',
})
export class EventModalPage {

  event = createDefaultProvidedCare();

  careGivers: CareGiver[];
  careType = CareType;
  selectableCareTypes: SelectableCareType[] = SelectableCareTypes;


  constructor(public navCtrl: NavController,
              private navParams: NavParams,
              public viewCtrl: ViewController,
              careGiverProvider: CareGiverProvider) {

    let selectedDay = this.navParams.get('selectedDay');
    let preselectedDate = moment(selectedDay).format();

    this.event.careGiver = this.navParams.get('careGiver');
    this.event.careType = this.navParams.get('careType');
    this.event.startTime = preselectedDate;
    this.event.endTime = preselectedDate;

    this.careGivers = [];

    careGiverProvider.getActiveCareGivers().then((loadedCareGivers) => {
      this.careGivers = loadedCareGivers;
    });
  }

  setDuration(duration) {
    this.event.duration = duration;
  }

  cancel() {
    this.viewCtrl.dismiss();
  }

  save() {
    this.viewCtrl.dismiss(this.event);
  }

}



